<template>
  <div class="row justify-content-center">
    <div class="col-12 col-md-3">
      <h2>Properties:</h2>
    </div>
    <div @click="route({ name: 'AdminPropCreate'})" class="col-8 col-md-3 btn btn-primary">
      Create
    </div>
    <div class="col-12 col-md-1">
    </div>
    <div @click="route({ name: 'AdminPropView'})" class="col-8 col-md-3 btn btn-primary">
      Edit
    </div>
    <div class="col-12 col-md-3">
      <h2 class="users">Users:</h2>
    </div>
    <div @click="route({ name: 'AdminUserCreate'})" class="col-8 col-md-3 btn btn-primary">
      Add Admin User
    </div>
    <div class="col-12 col-md-1">
    </div>
    <div @click="route({ name: 'AdminUserView'})" class="col-8 col-md-3 btn btn-primary">
      Edit Admins
    </div>
  </div>
</template>

<script>
  export default {
    name: 'adminHeader',
    data() {
      return {}
    },
    created() {},
    computed: {
      properties() {
        return this.$store.state.properties
      }
    },
    methods: {
      route(route) {
        this.$router.push(route)
      }

    }
  }

</script>

<style scoped>
  .adminHeader {
    margin-top: 5px;
  }

  .btn {
    margin-bottom: 50px;
  }

  .btn1 {
    margin-left: 55px;
  }



  p {
    margin-right: 20px;
    padding-right: 50px;
    font-size: 1.5rem;
  }

</style>



